import React from 'react';


function Card(props) {
    return (
        
        <div className="row">
            <div className="col-12">
                <div className="row d-flex justify-content-center">
                    <div className="col-11 col-md-10 col-lg-8 col-xl-7">
                        <h2 className="py-3">{props.id}{props.title}</h2>
                    </div>
                    <figure className="col-11 col-md-10 col-lg-8 col-xl-7 embed-responsive embed-responsive-16by9">
                        <img src={require(`../images/${props.src}`)} alt="Shoes" class="images embed-responsive-item"></img>
                        <figcaption><em>Images via Google Images</em></figcaption>
                    </figure>
                    <div className="col-11 col-md-10 col-lg-8 col-xl-7">
                        <p><strong>Brand:</strong> {props.brand} </p>
                        <p><strong>Release Year:</strong> {props.release}</p>
                        <p><strong>Purchase Year:</strong> {props.purchase}</p>
                        <p><strong>Rating:</strong> {props.rating}/10</p>
                        <br></br>
                        <p>{props.description}</p>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Card;