import React, { Component } from 'react';
import { Modal, ModalHeader, ModalBody } from 'reactstrap';


class Menu extends Component {
    state = {
        visible: true,
        modalIsOpen: false
    }
 
    toggleAlert() {
        this.setState({
            visible: !this.state.visible
        })
    }

    toggleModal() {
        this.setState({
            modalIsOpen: !this.state.modalIsOpen
        })
    }

    render() {
        return (
            <div>    
                <button onClick={this.toggleModal.bind(this)}><i title="Menu" className="fa fa-bars fa-lg ml-1"></i></button>
                <Modal isOpen={this.state.modalIsOpen}>
                    <ModalHeader toggle={this.toggleModal.bind(this)}>  
                        <img src={require(`../images/logo-black.png`)} alt="logo" className="py-2 embed-responive-item"></img> 
                    </ModalHeader>
                    <ModalBody>
                        <div className="modal-body">
                            <div className="container-fluid">
                            
                                <div className="row">
                                <span className="border-top"></span>
                                    <div className="col-6 my-2 py-4">
                                        <h2 className="ml-3">MUSIC</h2>
                                        <h2 className="ml-3">STYLE</h2>
                                        <h2 className="ml-3">POP CULTURE</h2>
                                        <h2 className="ml-3">SPORTS</h2>
                                        <h2 className="ml-3">LIFE</h2>
                                        <h2 className="ml-3">SNEAKERS</h2>
                                        <h2 className="ml-3">PIGEONS &amp; PLANES</h2>
                                        <h2 className="ml-3">SHOP</h2>
                                    </div>
                                    <div className="col-6 my-2 py-4">
                                        <h2 className="ml-3">SHOWS</h2>
                                        <h2 className="ml-3">PODCASTS</h2>
                                        <h2 className="ml-3">NEWSLETTERS</h2>
                                    </div>
                                    <span className="border-bottom"></span>
                                </div>
                                <div className="row d-flex justify-content-start mx-3 mb-3 py-4">
                                    <div className="col-12 col-md-8 col-xl-7 more-news-border">
                                        <div className="row py-1 pr-4">
                                            <div className="col-12">
                                                <p class="bold more-newsletter-title">NEWSLETTER</p>
                                            </div>
                                            <div className="col-12">
                                                <p className="more-newsletter-body"><em>Sign up for the Complex Newsletter for breaking news, events, and unique stories.</em></p>
                                            </div>
                                            <div className="more-newsletter row mb-4 ml-3">
                                                <div className="col-8 col-sm-10 col-md-11">    
                                                    <input className="pt-2" placeholder="EMAIL ADDRESS"></input>
                                                </div>
                                                <div className="col-4 col-sm-2 col-md-1 d-flex justify-content-end">
                                                    <button className="pb-1">SUBSCRIBE</button>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="col-6 my-2 py-4">
                                        <h6 className="ml-3">INTERNATIONAL</h6>
                                        <h2 className="ml-3">CANADA</h2>
                                        <h2 className="ml-3">UNITED KINGDOM</h2>
                                        <h2 className="ml-3">AUSTRALIA</h2>
                                    </div>
                                    <div className="col-6 my-2 py-4">
                                        <h6 className="ml-3">COMPLEX SITES</h6>
                                        <h2 className="ml-3">FIRST WE FEAST</h2>
                                        <h2 className="ml-3">SOLE COLLECTOR</h2>
                                        <h2 className="ml-3">COMPLEXCON</h2>
                                    </div>
                                    <span className="border-bottom"></span>
                                </div>
                                <div className="row">
                                    <div className="col-6 my-2 py-4">
                                        <h6 className="ml-3">STREAM ON</h6>
                                        <h2 className="ml-3">YOUTUBE</h2>
                                        <h2 className="ml-3">NETFLIX</h2>
                                        <h2 className="ml-3">HULU</h2>
                                        <h2 className="ml-3">ROKU</h2>
                                        <h2 className="ml-3">PLUTO.TV</h2>
                                        <h2 className="ml-3">XUMO</h2>
                                        <h2 className="ml-3">APPLETV</h2>
                                        <h2 className="ml-3">NETFLIX</h2>
                                        <h2 className="ml-3">HULU</h2>
                                    </div>
                                    <div className="col-6 my-2 py-4">
                                        <h6 className="ml-3">FOLLOW ON</h6>
                                        <h2 className="ml-3"><i title="Facebook" class="fa-sm fa fa-facebook-f mr-2"></i>FACEBOOK</h2>
                                        <h2 className="ml-3"><i title="Twitter" class="fa-sm fa fa-twitter mr-2"></i>TWITTER</h2>
                                        <h2 className="ml-3"><i title="Instagram" class="fa-sm fa fa-instagram mr-2"></i>INSTAGRAM</h2>
                                        <h2 className="ml-3"><i title="Snapchat" class="fa-sm fa fa-snapchat-ghost mr-2"></i>SNAPCHAT</h2>
                                        <h2 className="ml-3"><i title="Pinterest" class="fa-sm fa fa-pinterest mr-2"></i>PINTEREST</h2>
                                        <h2 className="ml-3"><i title="TikTok" class="fa-sm fa fa-headphones mr-2"></i>TIKTOK</h2>
                                    </div>
                                    <span className="border-bottom"></span>
                                </div>
                                <div className="row">
                                     <div className="col-6 my-2 py-4">
                                        <h6 className="ml-3">READ ON</h6>
                                        <h2 className="ml-3"><i title="Apple News" class="fa-sm fa fa-apple mr-2"></i>APPLE NEWS</h2>
                                        <h2 className="ml-3"><i title="Flipboard" class="fa-sm fa fa-bookmark mr-2"></i>FLIPBOARD</h2>
                                        <h2 className="ml-3"><i title="Google" class="fa-sm fa fa-google mr-2"></i>GOOGLE NEWS</h2>
                                        <h2 className="ml-3"><i title="RSS Feed" class="fa-sm fa fa-rss mr-2"></i>RSS FEED</h2>
                                    </div>
                                    <div className="col-6 my-2 py-4">
                                        <h6 className="ml-3">WORK WITH US</h6>
                                        <h2 className="ml-3">CAREERS</h2>
                                        <h2 className="ml-3">ADVERTISE</h2>
                                        <h2 className="ml-3">CONTACT US</h2>
                                    </div>
                                </div>
                                <div className="row more-footer-links no-gutters d-flex justify-content-start text-left pb-4">
                                    <div className="col col-sm-2 col-lg-2">
                                        <h6 className="ml-3">TERMS OF USE</h6>
                                    </div>
                                    <div className="col col-sm-3 col-lg-2">
                                        <h6 className="ml-3">PRIVACY POLICY</h6>
                                    </div>
                                    <div className="col col-sm-2 col-lg-1">
                                        <h6 className="ml-3">SITE MAP</h6>
                                    </div>
                                    <div className="col col-sm-2 col-lg-2">
                                        <h6 className="ml-3">PUBLIC NOTICE</h6>
                                    </div>
                                </div>
                                <footer className="row">
                                <span className="border-top"></span>

                                    <div className="col-12 col-md-11 pt-4">
                                        <p className="ml-3">COMPLEX participates in various affiliate 
                                            marketing programs, which means COMPLEX 
                                            gets paid commissions on purchases made 
                                            through our links to retailer sites. Our 
                                            editorial content is not influenced by any 
                                            commissions we receive.
                                        </p>
                                        <p className="ml-3">
                                            <i class="fa fa-copyright"></i> 
                                            2020 Complex Media, Inc. All Rights Reserved.
                                        </p>
                                    </div>
                                    <div className="col-12">
                                        <p className="ml-3">Complex.com is a part of<br></br>
                                            <img src={require(`../images/complex-networks.png`)} alt="logo" class="embed-responsive-item pt-3 images pl-1"></img>
                                        </p>
                                    </div>
                                </footer>
                            </div>
                        </div>    
                    </ModalBody>
                </Modal>
            </div>
        );
    }
}

export default Menu;