import React from 'react';
const banner = require('../images/banner3.jpg');

function Banner() {
    return (
        <div>
            <div className="row web">
                <div className="col-11 col-lg-8 col-xl-7 mx-auto">
                    <img src={banner} alt="banner" id="banner"></img>
                </div>
            </div>
            <div className="row mobile-title">
                <div className="col-11 col-lg-8 col-xl-7 mx-auto">
                    <h1 className="pb-4">A Complete Guide to Alex's Sneakers</h1>
                </div>
            </div>
            <div className="row">
                <div className="col-11 col-lg-6 col-xl-6 mx-auto">
                    <div className="row">
                        <div className="col-11 col-lg-8 col-xl-7 author">
                            <p>BY <a href="https://al-byte.github.io/Portfolio-Alt/">ALEX WASHINGTON</a></p>
                        </div>
                        <div className="col-11 col-lg-6 col-xl-6 text-right date">
                            <p>May 4, 2020</p>
                        </div>
                    </div>
                </div>
                <hr></hr>
            </div>
            <div className="row mobile">
                <div className="col">
                    <img src={banner} alt="banner" id="banner"></img>
                </div>
            </div>
            <div className="row">
                <div className="col-11 col-md-10 col-lg-8 col-xl-7 py-5 introduction mx-auto">
                    <p>Alex Washington: sneakerhead. Yup. Not in the traditional 
                        sense, but over the course of his life, Alex wore his fair 
                        share of Nike bangers and obscure white on white trainers. 
                        We thought documentation of this 1990s baby was warranted, 
                        so Complex tapped its go-to footwear historian Alex Washington 
                        to painstakingly sort through Alex's greatest hits. We've provied 
                        images when we had them, here they are in their entirety.
                    </p>
                    <p className="text-center bold"><strong>***DISCLAIMER!!!***</strong></p>
                    <em>This page is strictly a replica project of a Complex page article 
                        designed and developed with React. Most links are not active. I am 
                        in no way, shape, or form affiliated with Complex.com, Complex 
                        Magazine, Complex Networks, or any other incarnation of the Complex brand.
                    </em>
                </div>
                <span className="border-bottom"></span>
            </div>
        </div>
    )
}

export default Banner;