import React from 'react';
import More from './More.js';
import Menu from './Menu.js';

const logo = require('../images/logo.png');
const logoIcon = require('../images/logo-icon.jpg');
const shopIcon = require('../images/shop-icon.jpg');

function Nav() {
    return (
    <div>
        <nav className="row py-3 fixed-top mobile">
            <div className="col-3 text-left">
                <Menu />
            </div>
            <div className="col-6 text-center">
                <img src={logo} className="logo" alt="logo"></img>
            </div>
            <div className="col-3 text-right">
                <i title="Search" className="fa fa-search fa-lg"></i>
                <img src={shopIcon} className="shop-logo" alt="Logo"></img>
            </div>
        </nav>

        <nav className="row mr-auto py-3 fixed-top web">
            <div className="col-8 col-lg-2 pt-3 pl-4 d-flex justify-content-center">
                <img src={logo} className="logo" alt="logo"></img>
            </div>
            <div className="col-lg-7 text-center">
                <div className="row ml-4 py-4 no-gutters">
                    <div className="col-1">
                        <h6>MUSIC</h6>
                    </div>
                    <div className="col-1">
                        <h6>STYLE</h6>
                    </div>
                    <div className="col-2">
                        <h6>POP CULTURE</h6>
                    </div>
                    <div className="col-1">
                        <h6>SPORTS</h6>
                    </div>
                    <div className="col-1">
                        <h6>LIFE</h6>
                    </div>
                    <div className="col-2">
                        <h6 className="active">SNEAKERS</h6>
                    </div>
                    <div className="col-1 icon">
                        <img src={logoIcon} className="logo" alt="Logo"></img>
                        <h6>SHOP</h6>
                    </div>
                    <div className="col-1 mx-1">
                        <h6>SHOWS</h6>
                    </div>
                    <div className="col-1 icon more">
                        <h6><More /></h6>
                        <i title="More" className="fa fa-chevron-down ml-1"></i>
                    </div>
                </div>
            </div>
            <div className="col-lg-3 py-4">       
                <div className="row no-gutters">
                    <div className="col px-1">
                        <i title="Search" className="fa fa-search fa-lg"></i>
                    </div>
                    <div className="col-1">
                        <span className="pl-2">|</span>
                    </div>
                    <div className="col px-1">
                        <i title="Facebook" className="fa fa-facebook-f fa-lg"></i>
                    </div>
                    <div className="col px-1">
                        <i title="Twitter" className="fa fa-twitter fa-lg"></i>
                    </div>
                    <div className="col px-1">
                        <i title="Instagram" className="fa fa-instagram fa-lg"></i>
                    </div>
                    <div className="col px-1">
                        <i title="YouTube" className="fa fa-youtube fa-lg"></i>
                    </div>
                    <div className="col px-1">
                        <i title="Snapchat" className="fa fa-snapchat-ghost fa-lg"></i>
                    </div>
                    <div className="col px-1">
                        <i title="Tik-Tok" className="fa fa-headphones fa-lg"></i>
                    </div>                 
                </div>
            </div>
        </nav>
        <div className="row hide search">
            <div className="col-12">
                <div className="row d-flex justify-content-center">
                    <div className="col-8 text-center">
                        <input placeholder="Search"></input>
                    </div>
                    <div className="col-1">
                        <i title="Search" className="fa fa-search fa-lg"></i>
                    </div>
                </div>
            </div>
        </div>

    </div>

    )
}

export default Nav;